    /**
     * å¦å­ä¸ºç¡®å®äºä»¶å¤ç
     * @param solution æ°å¢çæ¥è¯¢æ¹æ¡
     */
    saveAsConfrimHandler(_event: any) {
        const solution: QuerySolutionVo = _event['querySolution'];
        if (solution.queryConditions && solution.queryConditions.length) {
            solution.queryConditions.forEach(element => {
                if (!!element.readonly) {
                    element.readonly = false;
                }
            });
        }
        this.httpService.create(solution, this.sessionId).subscribe(
            () => {
                //éæ°è®¾ç½®å¨å±çé»è®¤æ¹æ¡
                if (solution.isDefault) {//è¢«è®¾ç½®ä¸ºé»è®¤æ¹æ¡
                    this.defaultQuerySolution = solution;
                    this.querySolutionVos = this.querySolutionVos.map(vo => {
                        vo.isDefault = false;
                        return vo;
                    });
                }
                //ç¨æ·ä¿å­èªå·±çç­éæ¹æ¡åï¼ä¸åæ¾ç¤ºç³»ç»é»è®¤çæ¹æ¡
                if (this.querySolutionVos.length === 1 && this.querySolutionVos[0].isSystem) {
                    //ç¨æ·æ²¡æéç½®èªå·±çç­éæ¹æ¡æ¶ï¼æèç¨æ·åªéç½®äºä¸ä¸ªç­éæ¹æ¡æ¶ï¼æ­¤æ°ç»é½æä¸ä¸ªæ¹æ¡ï¼è¥æ­¤æ¹æ¡æ¯ç³»ç»é»è®¤æ¹æ¡ï¼ååºå¨å¦å­ä¸ºæ¶å æ
                    this.querySolutionVos = new Array<QuerySolutionVo>();
                }else if(this.querySolutionVos.length===2&&this.enableHistory){   
                    let filterSolutionVos = new Array<QuerySolutionVo>();                 
                    filterSolutionVos=this.querySolutionVos.filter(item=>{
                        if(item.type=='pre'){
                            return true;
                        }
                        return !item.isSystem
                    })
                    this.querySolutionVos=filterSolutionVos;
                }

                this.querySolutionVos.push(solution);
                this.selectedSolution = solution;
                this.showSaveButton = this.selectedSolution.isSystem ? false : true;
                this.selectedFieldConditions = this.selectedSolution.queryConditions;
                this.solutionService.showInfo(this.localeService.getValue("querySolution.container.saveSuccess"), 'success', '', 1500);
            },
            error => {
                this.solutionService.showInfo(this.localeService.getValue("querySolution.container.saveFail"), 'error', '', 1500);
                throw error;
            }
        );
    }

